import React from 'react'
import { Component } from 'react';


class Request extends Component {
 
    render() {
        const {text, lat, long, isAdd} = this.props;
        return (
            <div>
                <div className='block'>
                    
                    <p>Latitude : {lat}</p>
                    <p>Longitude : {long}</p>
                    <p>Reason : {text}</p>
                    {isAdd && (
                        <button className='xxq'>Add</button>
                    )}
                    {!isAdd && (
                        <button className='xxw' onClick={this.handleClick}>Remove</button>
                    )}      
                        
                </div>
            <br/>
            
        </div>
        )
    }
    
}

export default Request;
