import React from 'react'
import Navbar from './Navbar';
import { Map, GoogleApiWrapper, Marker, InfoWindow } from 'google-maps-react';
import { Component } from 'react';
import '../index.css'
import Button from './Button';
import ButtonRM from './ButtonRM'
import { useNavigate } from 'react-router-dom';



export class MapFrame extends Component {
   

    constructor(props) {
      super(props);
  
      this.state = {
        points: [],
        dataLoaded: false
      }
    }

    componentDidMount() {
      //Get request
        this.setState({points: [
          {latitude: 47.49855629475769, longitude: -122.14184416996333},
                {latitude: 47.359423, longitude: -122.021071},
                {latitude: 47.2052192687988, longitude: -121.988426208496},
                {latitude: 40.8907081, longitude: 29.5434325},
                {latitude: 40.984488, longitude: 29.2140121},
                {latitude: 40.984563, longitude: 29.324862}
              ]
        });

        /*fetch("https://artemisbalance.herokuapp.com/api/user/get_points", 
        {}) 
            .then(res => res.json())
            .then((json) => {
            this.setState({
              items: json,
              dataLoaded: true
            });
        })*/
    }
  
    displayMarkers = () => {
      return this.state.points.map((store, index) => {
        return <Marker key={index} id={index} position={{
         lat: store.latitude,
         lng: store.longitude
       }}
       onClick={() => this.handleClick(store)} />
      })
    }

    handleClick = (e) => {
      if (this.props.Rem) {
        fetch("https://artemisbalance.herokuapp.com/api/user/remove_point", {
          method: 'post',
          mode: 'no-cors',
          headers: {
            'Accept': 'application/json',
            'Content-type': 'application/json',
            'auth-token' : 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJfaWQiOiI2MWI3NWRjNWM0NWEyNzAwMTZjNzdmMzkiLCJpYXQiOjE2NDI3MjE4NTJ9.8KT0i_c8Qw-J9lpnaIGESVhHJNAw_BcCvYXDxwyf3p4'
          },
          body: JSON.stringify({
            latitude: e.latitude,
            longitude: e.longitude
          })
        });
        //POST request
        this.forceUpdate()
      }
      
    }
    
    onMapClicked = (e, map, coor) => {
      const { latLng } = coor;
      const lat = latLng.lat();
      const lng = latLng.lng();
      if (this.props.Add) {
        //Post Method
        fetch("https://artemisbalance.herokuapp.com/api/user/add_point", {
          method: 'post',
          mode: 'no-cors',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'auth-token' : 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJfaWQiOiI2MWI3NWRjNWM0NWEyNzAwMTZjNzdmMzkiLCJpYXQiOjE2NDI3MjE4NTJ9.8KT0i_c8Qw-J9lpnaIGESVhHJNAw_BcCvYXDxwyf3p4',
          },
          body: JSON.stringify({
            _id: '5',
            latitude: lat,
            longitude: lng,
            storage: '5'
          })
        });
      }
      
    }

    render() {
      const {dataLoaded, points} = this.state;
      if (dataLoaded) {
        return <div className='NNN'>
          <h1>Please wait</h1>
        </div>
      }
      return (
        <div>          
              <Map
                google={this.props.google}
                zoom={10}
                style={mapStyles}
                initialCenter={{ lat: 40.81, lng: 29.36}}
                onClick={this.onMapClicked}
               >
              {this.displayMarkers()}
                </Map>
          </div>
          
        
          
            
            
        
            
            
         
      );
    }
  }
export default GoogleApiWrapper({
    apiKey: 'AIzaSyDrLlGDGGxdkkT9nsIeEkjenFjcRzkf-UM'
  })(MapFrame);

  const mapStyles = {
    width: '100%',
    height: '95%',
  };

